import './App.css';
import React from 'react';
import SpaceScene from './SpaceScene';
import { C } from './C';
import { SelectPicker, Slider, Input } from 'rsuite';

const bodies = [
  { label: "", value: '1' },
  { label: "", value: '1-1' },
  { label: "", value: '1-2' },
  { label: "", value: '1-3' },
  { label: "", value: '1-3-1' },
  { label: "", value: '1-4' },
  { label: "", value: '1-5' },
  { label: "", value: '1-6' },
  { label: "", value: '1-7' },
  { label: "", value: '1-8' },
];
class App extends React.Component {
  state = {
    timeUnit: 1
  }
  scene: SpaceScene | null = null;
  componentDidMount() {
    this.scene = new SpaceScene();
  }
  render() {
    return (
      <div>
        <div id="scene" ></div>
        <div className='app-settings'>
          <label>(*)</label>
          <Slider defaultValue={1e3} min={1} max={1e4} onChange={(v: number) => {
            C.TIME_UNIT = 8.64 * v;
          }} />
          <br />
          <label>[,,]</label>
          <Input defaultValue="[10,200,10]" onChange={(val) => {
            C.fRadius = JSON.parse(val);
          }} 
          onPressEnter={(evt) => {
            this.scene?.updateFRadius();
          }} />
          <br />
          <label></label>
          <SelectPicker defaultValue='1' data={bodies} onChange={(v:string) => {
            this.scene?.updateCamera(v);
          }}/>
          <br />
        </div>
      </div>
    );
  }
}

export default App;
